













import Vue from "vue";
import Navbar from "./components/navigator/Navbar.vue";
import AdminNavbar from "./components/navigator/AdminNavbar.vue";
import Bottom from "./components/bottom/Bottom.vue";
import util from "./lib/util";
import { mapGetters } from "vuex";
import $ from "jquery";
import { Component, Watch } from "vue-property-decorator";
import { Route } from "vue-router";

@Component({
    components: {
        Navbar: Navbar,
        Bottom: Bottom,
        AdminNavbar: AdminNavbar
    },
    computed: mapGetters(["logined", "avatar", "admin", "user_id", "nick"])
})
export default class App extends Vue {
    homepage?: boolean;
    contest?: boolean;
    adminView?: boolean;
    $socket: any;
    sockets: any;
    created () {
        this.homepage = this.$route.path === "/";
        this.contest = this.isContestView(this.$route.fullPath);
        this.adminView = this.$route.fullPath.indexOf("/admin") === 0;
    }

    mounted () {
        this.$store.dispatch("NavStatus");
        util.initToTopButton();
        this.initWebSocket();
        setTimeout(() => {
            this.connectTry(10);
        }, 1500);
    }

    initWebSocket () {
        if (this.$socket && this.$socket.connect && this.$store.getters.logined) {
            this.$socket.connect();
        }
    }

    isContestView (path: string) {
        return path.includes("contest\/");
    }

    @Watch("$route")
    onRouteChange (to: Route) {
        this.homepage = to.path === "/";
        this.adminView = to.fullPath.indexOf("/admin") === 0;
        const matchedPath = to.matched[0].path.substring(1);
        this.contest = matchedPath !== "contest" && this.isContestView(matchedPath);
        this.$store.commit("setRouteInfo", { path: to.path, fullPath: to.fullPath });
        this.$store.commit("setHomepage", { homepage: this.homepage });
        this.$socket.emit("updateURL", { url: to.fullPath });
        this.initForRouter();
    }

    initForRouter () {
        this.$nextTick(() => {
            if (this.homepage) {
                $("#app").animate({ marginTop: 0 });
                $(".ui.borderless.network.secondary.menu").addClass("inverted");
            }
            else {
                $("#app").animate({ marginTop: "60px" });
                $(".ui.borderless.network.secondary.menu").removeClass("inverted");
            }
        });
    }

    connectTry (times: number) {
        if (!this.$store.getters.logined) {
            return;
        }
        (async () => {
            const Promise = require("bluebird");
            while (times-- > 0) {
                if (this.$socket && this.$socket.connect && typeof this.$socket.connect === "function") {
                    if (!this.$socket.connected) {
                        this.$socket.connect();
                        this.$socket.emit("getUser");
                    }
                    else {
                        break;
                    }
                }
                await Promise.delay(500);
            }
        })();
    }
}
